@keyframes visible1 {
  from {
    opacity: 0;
    /* left: 0; */
    transform: translateY(150px) translateX(150px);
  }
  to {
    opacity: 100;
    /* left: 1000; */
    transform: translateY(0px) translateX(0px);
  }
}
@keyframes comingFromRightAn {
  from {
    opacity: 0;
    /* left: 0; */

    transform: translateX(150px);
  }
  to {
    opacity: 100;
    /* left: 1000; */
    transform: translateX(0px);
  }
}
@keyframes comingFromLeftAn {
  from {
    opacity: 0;
    /* left: 0; */
    transform: translateX(-150px);
  }
  to {
    opacity: 100;
    /* left: 1000; */
    transform: translateX(0px);
  }
}

@keyframes comingFromUpAn {
  from {
    opacity: 0;
    /* left: 0; */
    transform: translateY(-150px);
  }
  to {
    opacity: 100;
    /* left: 1000; */
    transform: translateY(0px);
  }
}

@keyframes comingFromDownAn {
  from {
    opacity: 0;
    /* left: 0; */
    transform: translateY(150px);
  }
  to {
    opacity: 100;
    /* left: 1000; */
    transform: translateY(0px);
  }
}

@keyframes growOutAn {
  from {
    opacity: 0;
    width: 0%;
    height: 0%;
  }
  to {
    opacity: 100;
    width: 100%;
    height: 100%;
  }
}

/* .notification {
    animation: visible1 1s;
  } */
.growOutP5 {
  animation: growOutAn 0.5s ease-in-out;
}
.growOut {
  animation: growOutAn 1s ease-in-out;
}
.comingFromLeft {
  animation: comingFromLeftAn 1s ease-in-out;
}
.comingFromRight {
  animation: comingFromRightAn 1s ease-in-out;
}
.comingFromDown {
  animation: comingFromDownAn 1s ease-in-out;
}
.comingFromUp {
  animation: comingFromUpAn 1s ease-in-out;
}

.comingFromLeftP5 {
  animation: comingFromLeftAn 0.5s ease-in-out;
}
.comingFromRightP5 {
  animation: comingFromRightAn 0.5s ease-in-out;
}
.comingFromDownP5 {
  animation: comingFromDownAn 0.5s ease-in-out;
}
.comingFromUpP5 {
  animation: comingFromUpAn 0.5s ease-in-out;
}

@keyframes bkf {
  0% {
    color: grey;
  }
  50% {
    color: white;
  }
  100% {
    color: grey;
  }
}

@keyframes bkf {
  0% {
    color: grey;
  }
  50% {
    color: white;
  }
  100% {
    color: grey;
  }
}

@keyframes fadeUp {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

.backAndForth {
  animation: bkf 1s ease;
}

.fadeUp {
  animation: fadeUp 0.5s ease;
  transition: all 0.5s;
}

@keyframes fadeUpToBlue {
  0% {
    color: grey;
    opacity: 0;
    color: transparent;
  }

  50% {
    transform: translateY(-15px);
  }

  100% {
    /* opacity: 1; */
    color: #1976d2;
  }
}

.fadeUpToBlue {
  animation: fadeUpToBlue 0.5s ease;
  color: #1976d2;
}

@keyframes fadeUpToRed {
  0% {
    color: gray;
    opacity: 0;
    transform: translateY(0px);
  }
  50% {
    transform: translateY(15px);
  }

  100% {
    color: red;
  }
}

.fadeUpToRed {
  animation: fadeUpToRed 0.5s ease;
  color: Red;
}

@keyframes fadeAway {
  from {
    opacity: 1;

    transform: translateY(0px);
  }
  to {
    opacity: 0;

    transform: translateY(400px);
  }
}

.fadeAway {
  animation: fadeAway 0.3s ease;
}
@keyframes goBack {
  from {
    transform: translateX(0px);
    opacity: 1;
  }
  to {
    transform: translateX(400px);
    opacity: 0;
  }
}

.goBack {
  animation: goBack 0.6s ease;
}

.thinScroll::-webkit-scrollbar {
  width: 4px;
}
